import { useSession } from "next-auth/react"
import Sidebar from "../../components/main_layout"
import { useState } from "react"
import { useApi } from "../../lib"
import { OurApi } from "../../lib/ourapi"
import { XCircleIcon, CheckCircleIcon } from '@heroicons/react/solid'
import { PlusIcon } from '@heroicons/react/solid'
import SidebarNavigation from "../../components/sidebar_navigation"
import SettingSection from "../../components/setting_section"
import RoleSelector from "../../components/role_selector"

export default function ProjectSettings({ team_slug }) {
  const { data: session } = useSession({
    required: true
  })

  const { data: members, membersError, mutate: memberMutate } = useApi(`/api/${team_slug}/members`)

  const currentUserRole = members?.find(member => member.id === session?.user.id)?.role

  const [email, setEmail] = useState("")
  const [error, setError] = useState(null)
  const [success, setSuccess] = useState(false)
  const [loading, setLoading] = useState(false)

  if(members && members.message === 'Not authorized') {
    Router.push({
      pathname: '/dashboard'
    })
  }


  const onPost = async function(event) {
    event.preventDefault()
    setLoading(true)
    
    OurApi(`/api/${team_slug}/members`, "POST", session.accessToken, {
      email,
    }).then(({ data, isLoading, hasError }) => {
      if (hasError) {
        setError(data)
        setLoading(false)
        setSuccess(false)
      } else {
        memberMutate()
        setEmail("")
        setError(null)
        setSuccess(true)
        setLoading(false)
      }
      return data
    })
  }

  const onChangeRole = async function(member, role) {
    const response = await OurApi(`/api/${team_slug}/members/${member.id}`, "PUT", session.accessToken, {
      role
    })
    if (response.hasError) {
      setError(response.data)
    } else {
      memberMutate()
      setSuccess(true)
    }
  }

  const renderError = () => {
    return error && (
      <div className="rounded-md bg-red-50 p-4">
      <div className="flex">
        <div className="flex-shrink-0 flex content-center">
          <XCircleIcon className="h-5 w-5 text-red-400" aria-hidden="true" />
        </div>
        <div className="ml-3">
          <p className="text-md text-red-800">{error.details ? error.details : error.message}</p>
        </div>
      </div>
    </div>
    )
 }

 const renderSuccess = () => {
  return success && (
    <div className="rounded-md bg-green-50 p-4">
      <div className="flex">
        <div className="flex-shrink-0">
          <CheckCircleIcon className="h-5 w-5 text-green-400" aria-hidden="true" />
        </div>
        <div className="ml-3">
          <p className="text-sm font-medium text-green-800">Successfully updated</p>
        </div>
      </div>
    </div>
  )
 }

 const renderPersonInvitationStatus = (person) => {
  if(person.invitation_accepted) {
    return (
      <div className="flex items-center justify-center">
        <CheckCircleIcon className="h-5 w-5 text-green-400" aria-hidden="true" />
      </div>
    )
  }
  return (
    <div className="flex items-center justify-center">
      <XCircleIcon className="h-5 w-5 text-red-400" aria-hidden="true" />
    </div>
  )
 }

 const navigation = currentUserRole == 'owner' ? [
    { name: 'Settings', href: `/${team_slug}/settings`, current: false, icon: 'cog' },
    { name: 'Members', href: `/${team_slug}/members`, current: true, icon: 'cog' },
    { name: 'Billing', href: `/${team_slug}/settings/billing`, current: false, icon: 'cog' },
  ] : [
    { name: 'Settings', href: `/${team_slug}/settings`, current: false, icon: 'cog' },
    { name: 'Members', href: `/${team_slug}/members`, current: true, icon: 'cog' }
  ]


  return (
    <Sidebar team_slug={team_slug}>
        <SidebarNavigation navigation={navigation}>
          <SettingSection>
            <h1 className="text-2xl font-bold mb-4">Team Members</h1>
            {renderError()}
            {renderSuccess()}
            
            <div className="space-y-2">
              <form method="post" onSubmit={onPost}>
                <label htmlFor="add-team-members" className="block text-sm mb-1 font-medium text-gray-700 dark:text-gray-200">
                  Add Members
                </label>
                <p id="add-team-members-helper" className="sr-only">
                  Search by email address
                </p>
                <div className="flex">
                  <div className="flex-grow">
                    <input
                      type="text"
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      name="add-team-members"
                      id="add-team-members"
                      className="block w-full dark:bg-gray-700 shadow-sm focus:ring-sky-500 focus:border-sky-500 sm:text-sm border-gray-300 dark:border-0 rounded-md"
                      placeholder="Email address"
                      aria-describedby="add-team-members-helper"
                    />
                  </div>
                  <span className="ml-3">
                    <button
                      type="submit"
                      className="bg-white dark:bg-gray-700 inline-flex items-center px-4 py-2 border border-gray-300 dark:border-0 shadow-sm text-sm font-medium rounded-md text-gray-700 dark:text-gray-200 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-sky-500"
                    >
                      {loading ? (
                        <span className="spinner-border spinner-border-sm mr-1" role="status" aria-hidden="true"></span>
                      ) : (
                        <>
                          <PlusIcon className="-ml-2 mr-1 h-5 w-5 text-gray-400" aria-hidden="true" />
                          <span>Add</span>
                        </>
                      )}
                    </button>
                  </span>
                </div>
              </form>
              <div className="border-b border-gray-200">
                <ul role="list" className="divide-y divide-gray-200 dark:divide-gray-700">
                  {members && members.map((person) => (
                    <li key={person.email} className="py-4 flex">
                      {/*<img className="h-10 w-10 rounded-full" src={person.imageUrl} alt="" />**/}
                      <div className="flex flex-1">
                        {renderPersonInvitationStatus(person)}
                        <div className="flex flex-1 justify-between">
                          <div className="ml-3 flex flex-col">
                            <span className="text-sm font-medium text-gray-900 dark:text-gray-200">{person.name}</span>
                            <span className="text-sm text-gray-500">{person.email}</span>
                          </div>
                          {currentUserRole === "owner" && (
                            <div className="flex-col">
                              <RoleSelector role={person.role} onChange={(role) => onChangeRole(person, role)} />
                            </div>
                          )}
                        </div>
                      </div>
                    </li>
                  ))}
                </ul>
              </div>
            </div>
          </SettingSection>
        </SidebarNavigation>
    </Sidebar>
  )
}

export async function getServerSideProps(context) {
  // Pass data to the page via props
  return { props: { team_slug: context.params.team_slug } }
}