const host = process.env.NODE_ENV === 'production' ? 'https://app.errordeck.com' : 'http://localhost:5000'

export async function OurApi(url, method, token, sendData) {

  const newUrl = `${host}${url}`;
  let data = null

  const res = method == "GET" ? await fetch(newUrl, {
    method: method,
    headers: token ? {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      } : {
        'Content-Type': 'application/json'
      }
  }) : await fetch(newUrl, {
    method: method,
    headers: token ? {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      } : {
        'Content-Type': 'application/json'
      },
    body: sendData && JSON.stringify(sendData)
  });

  try {
    data = await res.json();
  }
  catch (e) {
    data = null
  }

  const hasError = res.status >= 400;

  return {
    data,
    hasError
  }
}