export default function RoleSelector({role = "member", onChange}) {
  return (
    <div>
      <select
        id="role"
        name="role"
        className="mt-1 block w-full pl-3 pr-10 py-2 dark:bg-slate-700 dark:text-slate-200 text-base border-gray-300 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm rounded-md"
        defaultValue={role}
        onChange={(e) => onChange(e.target.value)}
      >
        <option value="owner">Owner</option>
        <option value="member">Member</option>
      </select>
    </div>
  )
}